<template>
    <div class="asset-actions">
        <button class="collect-button" @click = "collectAssets">Collect Asset</button>
        <button class="edit-button">Edit Asset</button>
        <button class="delete-button" @click = "deleteAsset">Delete Asset</button>
        <button class="save-button">Download</button>
    </div>
  </template>
  
  <script>
  import axios from 'axios'
  export default {
    name: 'AssetActions',
    props: ['assetId'],
    data() {
      return {
        teacherId: 1,
      };
    },
    methods: {
      deleteAsset() {
        axios.delete(`/api/asset/remove/${this.assetId}`)
            .then(response => {
              if (response.data.code === 1) {
                alert('Asset is deleted successfully');
                this.$router.push('/');
              } else {
                alert('Error deleting assets: ' + response.data.msg);
              }
            })
            .catch(error => {
              console.error('Error deleting assets', error);
            });
      },
      collectAssets() {
        axios.post('/api/asset/collect', null,{
          params:{
            teacherId: this.teacherId,
            assetId: this.assetId,
          }
        })
            .then(response => {
              alert(response.data.msg || 'Assets successfully collected');
            })
            .catch(error => {
              console.error('Error collecting assets', error.response ? error.response.data : error);
              alert('Error collecting assets');
            });
      }
    }
  }
  </script>
  
  <style scoped>
  .asset-actions {
    width: 310px;
    height: 900px;
    display: flex;
    flex-direction: column;
  }
  
  .collect-button{
    background-color: #1E2A50;
    color: white;
    border: none;
    margin-top: 200px;
    margin-left: 65px;
    font-size: 16px;
    width: 150px;
    height: 40px;
    text-align: center;
  }
  
  .edit-button, .delete-button, .save-button{
    background-color: #1E2A50;
    color: white;
    border: none;
    margin-top: 40px;
    margin-left: 65px;
    cursor: pointer;
    font-size: 16px;
    width: 150px;
    height: 40px;
    text-align: center;
  }

  .save-button:hover, .edit-button:hover,.collect-button, .delete-button:hover {
    background-color: #16395B;
  }
  </style>
  